import logo from './logo.svg';
import './App.css';
import gsap from 'gsap'
import {useEffect,useRef} from 'react'
import HeaderComp from './components/HeaderComp'
import InfoComp from './components/infoComp'

import { ScrollTrigger } from 'gsap/ScrollTrigger';



function App() {
  
gsap.registerPlugin(ScrollTrigger);
  
  useEffect(()=>{
    const tl2=gsap.timeline();

    gsap.to("body",0,{css:{visibility:"visible"}});

    tl2.from(".heads",1.8 ,{
            autoAlpha: 0,
            y:100,
              
            ease: "power4.out",
            delay: 0.3,
           
            stagger:{
              amount:0.4
            }
        })
    .to(".headw1",1.6,{
          height:0,
          ease:"expo.inOut",
          stagger:0.4
        })
        .to(".heads",0,{
            css:{color:"black"},
            delay:-1.55
        })
    .to(".hw1",1.6,{
            width:0,
            ease:"expo.inOut",
            stagger:0.4,
            delay:-0.8
          })
    .to(".herowrapper",0,{css:{display:"none"}})
    .to(".headerwrapper",0,{css:{display:"none"}})
    .from(".imgholder img",1.6,{
      scale:1.4,
      delay:-2,
      stagger:{amount:0.4}
    })
       
       gsap.from(".infoh1",1.6 ,{
      scrollTrigger:{
        trigger:'.infoh1',
        toggleActions:"restart none none none"
        
      } ,
      y: 50,
      stagger:{amount:0.4},
      autoAlpha: 0
    });  


        gsap.from(".infoh2",1.6 ,{
      scrollTrigger:{
        trigger:'.infoh2',
        toggleActions:"restart none none none"
        
      } ,
      y: 50,
       stagger:{amount:0.4},
      autoAlpha: 0
    });

         gsap.from(".infoh3",1.6 ,{
      scrollTrigger:{
        trigger:'.infoh3',
        toggleActions:"restart none none none"
        
      } ,
      y: 50,
       stagger:{amount:0.4},
      autoAlpha: 0
    });
       


     let duration = 10,
    sections = gsap.utils.toArray(".panel"),
    sectionIncrement = duration / (sections.length - 1),
    tl = gsap.timeline({
      scrollTrigger: {
        trigger: ".containerr",
        pin: true,
        scrub: 1,
        snap: 1 / (sections.length - 1),
        start: "top top",
        end: "+=5000"
      }
    });

tl.to(sections, {
  xPercent: -100 * (sections.length - 1),
  duration: duration,
  ease: "none"
});



// everything below this is just for the fading/scaling up which is NOT scrubbed - it's all dynamic, triggered when each section enters/leaves so that the fading/scaling occurs at a consistent rate no matter how fast you scroll!
sections.forEach((section, index) => {
  let tween = gsap.from(section, {
    opacity: 0, 
    scale: 0.6, 
    duration: 1, 
    force3D: true, 
    paused: true
  });
  addSectionCallbacks(tl, {
    start: sectionIncrement * (index - 0.99),
    end: sectionIncrement * (index + 0.99),
    onEnter: () => tween.play(),
    onLeave: () => tween.reverse(),
    onEnterBack: () => tween.play(),
    onLeaveBack: () => tween.reverse()
  });
  index || tween.progress(1); // the first tween should be at its end (already faded/scaled in)
});





// helper function that lets us define a section in a timeline that spans between two times (start/end) and lets us add onEnter/onLeave/onEnterBack/onLeaveBack callbacks
function addSectionCallbacks(timeline, {start, end, param, onEnter, onLeave, onEnterBack, onLeaveBack}) {
  let trackDirection = animation => { // just adds a "direction" property to the animation that tracks the moment-by-moment playback direction (1 = forward, -1 = backward)
    let onUpdate = animation.eventCallback("onUpdate"), // in case it already has an onUpdate
        prevTime = animation.time();
    animation.direction = animation.reversed() ? -1 : 1;
    animation.eventCallback("onUpdate", () => {
      let time = animation.time();
      if (prevTime !== time) {
        animation.direction = time < prevTime ? -1 : 1;
        prevTime = time;
      }
      onUpdate && onUpdate.call(animation);
    });
  },
      empty = v => v; // in case one of the callbacks isn't defined
  timeline.direction || trackDirection(timeline); // make sure direction tracking is enabled on the timeline
  start >= 0 && timeline.add(() => ((timeline.direction < 0 ? onLeaveBack : onEnter) || empty)(param), start);
  end <= timeline.duration() && timeline.add(() => ((timeline.direction < 0 ? onEnterBack : onLeave) || empty)(param), end);
}



       
  },[])

  return (
    <>
    <HeaderComp/>
    <InfoComp/>
    </>
  );
}

export default App;
